.box{
 
    margin-left: 40px;
    margin-right: 40px;
    margin-bottom: 20px;
    margin-top: 20px;
     text-align: center;
     border-style: solid;
     border-radius: 3vh;
     place-items: center;
     box-shadow: 10px -4px lightgray;
     color:rgb(88, 80, 80);
     min-height: 40vh;
     align-items: center;
    padding: 5px;
}

.active{
  border-color: green;
}

.spect{
  font-weight: bold;
}

.goal{
  text-decoration-thickness: auto;
  text-decoration-thickness: from-font;
}

.NavBarItems{
  background: linear-gradient(98deg, rgb(110, 110, 114) 10%, rgb(192, 192, 199) 100%);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar-logo{
  color: #ffffff;
  justify-content: start;
  margin-left: 20px;
  cursor: pointer;
}

.nav-menu{
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  text-align: center;
  list-style: none;
  width: 70vw;
  justify-content: end;
  margin-right:  2rem;
}

.nav-links{
  color: #ffffff;
  text-decoration: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
}

.nav-links:hover{
  background-color: #333;
  border-radius: 4px;
  transition: all 0.2s ease-out;
}

.navbar-logo{
  font-family: 'Unica One', cursive;
  font-size: xx-large;
}

.footerbox{
 color: white;
 background-color:black;
 min-height:50px;
 margin-top: auto;
}



