p.navbar-p{
  margin-left: 12vw;
  margin-right: 12vw;
  margin-top: 2vh;
  margin-bottom: 7vh;
  cursor: pointer;
}

p.navbar-p:hover{
  
  color: royalblue;
  transition: all 0.3s ease;
}

.menu-icon{
  display: none;
}


.formbox{
  display: flex;
}

.form{
  width: 50%;
  position: relative;
  display: inline-block;
  align-self: center;
  
}

.form input{
  width: 100%;
  height: 100%;
  color: #aaa;
  padding-top: 20px;
  font-size: x-large;
  align-items: center;
  margin-bottom: 10px;
  background-color: #eee;
  border-top: none;
  border-left: none;
  border-right: none;
}

.form label {
  position: absolute;
  bottom: 0px;
  left: 0%;
  width: 100%;
  height: 100%;
  pointer-events: none;

}



.form2 input{
  width: 10%;
  height: 50%;
  color: #aaa;
  padding-top: 20px;
  font-size: large;
  align-items: center;
  margin-bottom: 10px;
  margin-left: 5px;
  margin-right: 5px;
  background-color: #eee;
  border-top: none;
  border-left: none;
  border-right: none;
  display: inline-block;
}

.cbut{
  margin: 10px;
}

.transition-enter{
  opacity: 0;
  transform: translate(-10px, 0px);
  transition: all 0.5s;
}

.transition-enter-active{
  opacity: 1;
  transform: translate(10px, 0px);
  transition: all 0.5s;
}

.transition-enter-done{
  opacity: 1;
 
}

.transition-exit-active{
  opacity: 1;
  transform: translate(10px, 0);
  transition: all 0.5s;
}

.transition-exit{
  opacity: 0;
  transform: translate(-50px, 0px);
  transition: all 0.5s;
}

.transition-exit-done{
  opacity: 0;
 
}

.container{
  display: grid;
  column-gap: 3px;
  row-gap: 4px;
  grid-template-columns: 20% 20% 20% 20% 20%;
  grid-template-rows: 50% 50%;
  grid-template-areas: 
  "a a a s s"
  "b b b s s";
}

.container div{
 margin: 3px;
}

.item-a{
  background: red;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 2;
  grid-area: a;
  justify-self: none;
  align-self: baseline;
}

.item-b{
  background: orange;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-area: s
}

.item-c{
  grid-area: b;
  background: yellow;
}








