:root {
    --primary: #444;
    
}

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer
}

.btn:hover{
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    background: #ffffff;
    color: #6568F4;
    transition: 250ms;
}

.btn--primary {
    background-color: var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #ffffff;
    padding: 8px 20px;
    border-radius:  4px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--small{
    padding: 4px 16px;
    border-radius: 4px;
    font-size: 14px;
    color: #fff;
    margin: 2px
}

.btn--medium {
    padding: 8px 20px;
    border-radius: 4px;
    font-size: 18px;
    color: #fff;
    margin: 10px;
}

.btn-large {
    padding: 12px 26px;
    border-radius: 4px;
    font-size: 20px;
    color: #fff;
}

